.footer {
  padding: 50px 0;
  position: relative;
  .social-menu {
    justify-self: center;
    &__link {
      fill: white;
    }
  }
}
.footer__bgimg {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: url("../img/bread-2.jpg") no-repeat;
  background-image: -webkit-image-set(url("../img/bread-2.webp") 1x);
  background-size: cover;
  z-index: -1;
  background-attachment: fixed;
  filter: brightness(0.5) contrast(0.9);
}
.footer__container {
  display: grid;
  gap: 20px;
  @media (min-width: $width-tablet+px) {
    grid-template-columns: 1fr 2fr 1fr;
    gap: 0;
  }
}
.container {
}
.header__logo {
}
.footer__menu {
}
.footer-menu {
  display: flex;
  align-items: center;
  align-self: center;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 10px;
}
.footer-menu__li {
}
.footer-menu__link {
  color: white;
}
