.promo {
  padding: 50px 0;
}
.promo__recieps {
  display: grid;
  gap: 20px;
  grid-template-columns: 1fr;
  padding: 25px 0;
  @media (min-width: $width-tablet+px) {
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: min-content repeat(3, 1fr) 20px;
  }
}
.recieps {
}
.recieps__item {
}
.recieps-item {
  min-height: 100%;
}
.recieps-item__img {
  height: 100%;
  width: 100%;
  position: relative;
  img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
  }
}
.recieps-item__title {
  font-weight: 400;
  font-size: clamp(18px, 2vw, 20px);
}
.promo-title-main {
  font-size: clamp(18px, 3vw, 32px);
  position: relative;
  &::after {
    margin-left: 10px;
    content: "";
    position: absolute;
    top: 50%;
    width: 100%;
    height: 2px;
    background: var(--main-color);
  }
}
.recieps-item__text {
}
.recieps-item--heading {
  overflow: hidden;
  height: min-content;
  grid-column: 1/-1;
  @media (min-width: $width-tablet+px) {
    grid-column: -1/-3;
  }
}
.recieps-item--lg {
  display: flex;
  flex-direction: column;
  @media (min-width: $width-tablet+px) {
    grid-column: 1/5;
    grid-row: 1/-1;
  }
  .recieps-item__img {
    width: 100%;
    min-height: 300px;
  }
  .recieps-item__title {
    font-size: clamp(18px, 3vw, 32px);
  }
  .recieps-item__text {
    color: #777;
    font-size: 14px;
  }
}
.recieps-item--sm {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 10px;
  height: 200px;
  @media (min-width: $width-tablet+px) {
    grid-column: -1/-3;
  }
  .recieps-item__title {
    flex: 0 1 auto;
  }
}

// link to start
.tostart {
  padding: 25px 0;
  border-top: 2px solid goldenrod;
  border-bottom: 2px solid goldenrod;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 25px;
}
.tostart__logo {
  display: flex;
  border-radius: 50%;
  overflow: hidden;
}
.tostart__text {
  display: block;
  max-width: 320px;
  min-width: 280px;
}

// begin sour block
.begin-sour {
  padding: 50px 0;
}
.begin-sour__title {
  overflow: hidden;
  margin-bottom: 20px;
}

.begin-sour__wrapper {
  display: block;
  padding: 25px;
  background: black url("../img/sourdought.jpg") no-repeat;
  background-size: cover;
  color: white;
  filter: hue-rotate(20deg) brightness(0.8) saturate(0.8);
  transition: all 0.7s ease 0s;
  &:hover {
    filter: none;
  }
}
.begin-sour__text {
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: normal;
}
.begin-sour__pseudo-btn {
  transition: all 0.7s ease 0s;
  display: inline-block;
  vertical-align: middle;
  font-size: 15px;
  padding: 12px 24px;
  border-radius: 3px;
  border: 2px solid #fff;
  &:hover {
    border-color: #3154da;
  }
}
.begin-sour-item__title {
  line-height: 30px;
}
