.begin-sour__content {
  display: grid;
  max-width: 100%;
  gap: 10px;
  @media (min-width: $width-tablet+px) {
    grid-template-columns: repeat(3, 1fr);
  }
}
.begin-sour__wrapper {
}
.begin-sour__text {
}
.begin-sour__pseudo-btn {
}
.begin-sour__item {
}
.begin-sour-item {
  border: 3px solid var(--main-color);
  border-radius: 8px;
  text-align: center;
  transition: all 0.5s ease 0s;
  @media (min-width: $width-tablet+px) {
    &:not(.begin-sour-item--lg):hover {
      transform: translateY(-5px);
      box-shadow: 4px 4px 6px 0px #00000030;
      img {
        transform: scale(1.1);
      }
    }
  }
}
.begin-sour-item--lg {
  grid-column: 1/-1;
  text-align: left;
}
.begin-sour-item__img-wrapper {
  width: 100%;
  height: 220px;
  position: relative;
  overflow: hidden;
  img {
    transition: all 0.5s ease 0s;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: contain;
  }
}
.begin-sour-item__title {
  font-weight: 400;
  font-size: clamp(18px, 2vw, 20px);
  color: black;
}
