.header {
  padding: 20px 0px;
  color: black;
}
.header-top {
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  @media (min-width: $width-tablet+px) {
    align-items: center;
    grid-template-columns: repeat(3, 1fr);
  }
  @media (min-width: 1100px) {
    margin-bottom: 20px;
    align-items: start;
  }
}
.header__social-wrapper {
  justify-self: center;
  position: absolute;
  top: 0;
  left: 0;
  @media (min-width: $width-tablet+px) {
    position: relative;
  }
  @media (min-width: $width-tablet+px) {
    justify-self: start;
  }
}
.header__logo {
  display: flex;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  align-items: center;
  justify-content: center;
  background: sandybrown;
  justify-self: center;
}
.header__login {
  justify-self: center;
  @media (min-width: $width-tablet+px) {
    justify-self: end;
  }
}
.login-btn {
}
.login-btn__text {
}
.header-bottom {
}
.header__nav {
  text-align: center;
  position: relative;
  width: 100%;
  background: #ffffe8;
  height: 35px;
}
.header__nav-toggle {
  position: relative;
  z-index: 30;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35px;
  height: 35px;
  @media (min-width: $width-tablet+px) {
    display: none;
  }
  &[aria-expanded="true"] {
    .header__nav-toggle-icon {
      background: transparent;
    }
    .header__nav-toggle-icon::after {
      transform: rotate(-225deg);
      top: 0;
      background: var(--main-color);
    }
    .header__nav-toggle-icon::before {
      transform: rotate(225deg);
      top: 0;
      background: var(--main-color);
    }
  }
  .header__nav-toggle-icon {
    display: block;
    width: 20px;
    height: 2px;
    background: black;
    position: relative;
    &:after {
      transition: all 0.5s ease 0s;
      content: "";
      position: absolute;
      top: -8px;
      left: 0;
      width: 100%;
      height: 2px;
      background: inherit;
    }
    &:before {
      transition: all 0.5s ease 0s;
      content: "";
      position: absolute;
      top: 8px;
      left: 0;
      width: 100%;
      height: 2px;
      background: inherit;
    }
  }
}
.header__nav-menu {
}
.nav-menu {
  display: flex;
  align-items: center;
  justify-content: space-around;
  background: #ffffe8;
  @media (max-width: $width-tablet+px) {
    z-index: -1;
    opacity: 0;
    visibility: hidden;
    flex-direction: column;
    transform: translateY(-100%);
    transition: transform 0.5s ease 0s, opacity 0.5s ease 0.2s;
  }
}
.nav-menu--active {
  opacity: 1;
  visibility: visible;
  display: flex;
  position: absolute;
  top: 35px;
  left: 0;
  right: 0;
  z-index: 20;
  transform: translate(0);
}
.nav-menu__item {
}
.nav-menu__link {
  color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 16px;
  position: relative;
  text-transform: uppercase;
  letter-spacing: 1px;
  &--active:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 2px;
    width: 100%;
    background: var(--main-color);
  }
}
