@charset "UTF-8";
:root {
  --main-color:rgba(244, 164, 96, 0.5); }

* {
  padding: 0px;
  margin: 0px;
  border: 0px; }

*,
*:before,
*:after {
  box-sizing: border-box; }

aside,
nav,
footer,
header,
section {
  display: block; }

html,
body {
  height: 100%; }

body {
  font-family: "Roboto", Arial, sans-serif;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

input,
button,
textarea {
  font-family: "Roboto", Arial, sans-serif; }

input::-ms-clear {
  display: none; }

button {
  cursor: pointer; }

button::-moz-focus-inner {
  padding: 0;
  border: 0; }

a,
a:visited {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

ul li {
  list-style: none; }

img {
  vertical-align: top; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit; }

/* шрифты */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: local("Roboto"), url("../fonts/roboto.woff2") format("woff2"), url("../fonts/roboto.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: local("Roboto medium"), url("../fonts/robotomedium.woff2") format("woff2"), url("../fonts/robotomedium.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: bold;
  src: local("Roboto bold"), url("../fonts/robotobold.woff2") format("woff2"), url("../fonts/robotobold.woff") format("woff");
  font-display: swap; }

.container {
  box-sizing: border-box;
  min-width: 320px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto; }
  @media (min-width: 374.98px) {
    .container {
      padding-left: 50px;
      padding-right: 50px;
      max-width: 100%; } }
  @media (min-width: 767.98px) {
    .container {
      padding-left: 50px;
      padding-right: 50px; } }
  @media (min-width: 1300px) {
    .container {
      max-width: 1300px; } }

.container-fluid {
  min-width: 320px;
  max-width: 100%; }

.main-btn {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 0;
  outline: 0;
  background-image: var(--btn-gradient);
  text-decoration: none;
  color: var(--basic-black);
  font-weight: bold;
  box-shadow: var(--btn-shadow);
  text-shadow: var(--btn-text-shadow); }
  .main-btn:hover {
    background-image: var(--btn-gradient-hover); }
  .main-btn:active {
    -webkit-box-shadow: var(--btn-shadow-active);
    box-shadow: var(--btn-shadow-active);
    background-image: var(--btn-gradient-active); }
  .main-btn:focus, .main-btn:focus-within {
    outline: none;
    border: 2px solid var(--input-focus-border); }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.promo {
  padding: 50px 0; }

.promo__recieps {
  display: grid;
  gap: 20px;
  grid-template-columns: 1fr;
  padding: 25px 0; }
  @media (min-width: 767.98px) {
    .promo__recieps {
      grid-template-columns: repeat(6, 1fr);
      grid-template-rows: min-content repeat(3, 1fr) 20px; } }

.recieps-item {
  min-height: 100%; }

.recieps-item__img {
  height: 100%;
  width: 100%;
  position: relative; }
  .recieps-item__img img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover; }

.recieps-item__title {
  font-weight: 400;
  font-size: clamp(18px, 2vw, 20px); }

.promo-title-main {
  font-size: clamp(18px, 3vw, 32px);
  position: relative; }
  .promo-title-main::after {
    margin-left: 10px;
    content: "";
    position: absolute;
    top: 50%;
    width: 100%;
    height: 2px;
    background: var(--main-color); }

.recieps-item--heading {
  overflow: hidden;
  height: min-content;
  grid-column: 1/-1; }
  @media (min-width: 767.98px) {
    .recieps-item--heading {
      grid-column: -1/-3; } }

.recieps-item--lg {
  display: flex;
  flex-direction: column; }
  @media (min-width: 767.98px) {
    .recieps-item--lg {
      grid-column: 1/5;
      grid-row: 1/-1; } }
  .recieps-item--lg .recieps-item__img {
    width: 100%;
    min-height: 300px; }
  .recieps-item--lg .recieps-item__title {
    font-size: clamp(18px, 3vw, 32px); }
  .recieps-item--lg .recieps-item__text {
    color: #777;
    font-size: 14px; }

.recieps-item--sm {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 10px;
  height: 200px; }
  @media (min-width: 767.98px) {
    .recieps-item--sm {
      grid-column: -1/-3; } }
  .recieps-item--sm .recieps-item__title {
    flex: 0 1 auto; }

.tostart {
  padding: 25px 0;
  border-top: 2px solid goldenrod;
  border-bottom: 2px solid goldenrod;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 25px; }

.tostart__logo {
  display: flex;
  border-radius: 50%;
  overflow: hidden; }

.tostart__text {
  display: block;
  max-width: 320px;
  min-width: 280px; }

.begin-sour {
  padding: 50px 0; }

.begin-sour__title {
  overflow: hidden;
  margin-bottom: 20px; }

.begin-sour__wrapper {
  display: block;
  padding: 25px;
  background: black url("../img/sourdought.jpg") no-repeat;
  background-size: cover;
  color: white;
  filter: hue-rotate(20deg) brightness(0.8) saturate(0.8);
  transition: all 0.7s ease 0s; }
  .begin-sour__wrapper:hover {
    filter: none; }

.begin-sour__text {
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: normal; }

.begin-sour__pseudo-btn {
  transition: all 0.7s ease 0s;
  display: inline-block;
  vertical-align: middle;
  font-size: 15px;
  padding: 12px 24px;
  border-radius: 3px;
  border: 2px solid #fff; }
  .begin-sour__pseudo-btn:hover {
    border-color: #3154da; }

.begin-sour-item__title {
  line-height: 30px; }

.begin-sour__content {
  display: grid;
  max-width: 100%;
  gap: 10px; }
  @media (min-width: 767.98px) {
    .begin-sour__content {
      grid-template-columns: repeat(3, 1fr); } }

.begin-sour-item {
  border: 3px solid var(--main-color);
  border-radius: 8px;
  text-align: center;
  transition: all 0.5s ease 0s; }
  @media (min-width: 767.98px) {
    .begin-sour-item:not(.begin-sour-item--lg):hover {
      transform: translateY(-5px);
      box-shadow: 4px 4px 6px 0px #00000030; }
      .begin-sour-item:not(.begin-sour-item--lg):hover img {
        transform: scale(1.1); } }

.begin-sour-item--lg {
  grid-column: 1/-1;
  text-align: left; }

.begin-sour-item__img-wrapper {
  width: 100%;
  height: 220px;
  position: relative;
  overflow: hidden; }
  .begin-sour-item__img-wrapper img {
    transition: all 0.5s ease 0s;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: contain; }

.begin-sour-item__title {
  font-weight: 400;
  font-size: clamp(18px, 2vw, 20px);
  color: black; }

.start {
  height: 40vh;
  min-height: 225px;
  background: white url("../img/bread-3.jpg") no-repeat;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  padding: 50px 0; }

.start__title {
  font-size: clamp(20px, 4vw, 40px);
  display: inline-block;
  line-height: 100%; }

.header {
  padding: 20px 0px;
  color: black; }

.header-top {
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto; }
  @media (min-width: 767.98px) {
    .header-top {
      align-items: center;
      grid-template-columns: repeat(3, 1fr); } }
  @media (min-width: 1100px) {
    .header-top {
      margin-bottom: 20px;
      align-items: start; } }

.header__social-wrapper {
  justify-self: center;
  position: absolute;
  top: 0;
  left: 0; }
  @media (min-width: 767.98px) {
    .header__social-wrapper {
      position: relative; } }
  @media (min-width: 767.98px) {
    .header__social-wrapper {
      justify-self: start; } }

.header__logo {
  display: flex;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  align-items: center;
  justify-content: center;
  background: sandybrown;
  justify-self: center; }

.header__login {
  justify-self: center; }
  @media (min-width: 767.98px) {
    .header__login {
      justify-self: end; } }

.header__nav {
  text-align: center;
  position: relative;
  width: 100%;
  background: #ffffe8;
  height: 35px; }

.header__nav-toggle {
  position: relative;
  z-index: 30;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35px;
  height: 35px; }
  @media (min-width: 767.98px) {
    .header__nav-toggle {
      display: none; } }
  .header__nav-toggle[aria-expanded="true"] .header__nav-toggle-icon {
    background: transparent; }
  .header__nav-toggle[aria-expanded="true"] .header__nav-toggle-icon::after {
    transform: rotate(-225deg);
    top: 0;
    background: var(--main-color); }
  .header__nav-toggle[aria-expanded="true"] .header__nav-toggle-icon::before {
    transform: rotate(225deg);
    top: 0;
    background: var(--main-color); }
  .header__nav-toggle .header__nav-toggle-icon {
    display: block;
    width: 20px;
    height: 2px;
    background: black;
    position: relative; }
    .header__nav-toggle .header__nav-toggle-icon:after {
      transition: all 0.5s ease 0s;
      content: "";
      position: absolute;
      top: -8px;
      left: 0;
      width: 100%;
      height: 2px;
      background: inherit; }
    .header__nav-toggle .header__nav-toggle-icon:before {
      transition: all 0.5s ease 0s;
      content: "";
      position: absolute;
      top: 8px;
      left: 0;
      width: 100%;
      height: 2px;
      background: inherit; }

.nav-menu {
  display: flex;
  align-items: center;
  justify-content: space-around;
  background: #ffffe8; }
  @media (max-width: 767.98px) {
    .nav-menu {
      z-index: -1;
      opacity: 0;
      visibility: hidden;
      flex-direction: column;
      transform: translateY(-100%);
      transition: transform 0.5s ease 0s, opacity 0.5s ease 0.2s; } }

.nav-menu--active {
  opacity: 1;
  visibility: visible;
  display: flex;
  position: absolute;
  top: 35px;
  left: 0;
  right: 0;
  z-index: 20;
  transform: translate(0); }

.nav-menu__link {
  color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 16px;
  position: relative;
  text-transform: uppercase;
  letter-spacing: 1px; }
  .nav-menu__link--active:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 2px;
    width: 100%;
    background: var(--main-color); }

.social-menu {
  display: flex;
  align-items: center;
  gap: 20px; }

.social-menu__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: coral; }

@media (max-width: 767.98px) {
  .header .social-menu {
    flex-direction: column; } }

.footer {
  padding: 50px 0;
  position: relative; }
  .footer .social-menu {
    justify-self: center; }
    .footer .social-menu__link {
      fill: white; }

.footer__bgimg {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: url("../img/bread-2.jpg") no-repeat;
  background-image: -webkit-image-set(url("../img/bread-2.webp") 1x);
  background-size: cover;
  z-index: -1;
  background-attachment: fixed;
  filter: brightness(0.5) contrast(0.9); }

.footer__container {
  display: grid;
  gap: 20px; }
  @media (min-width: 767.98px) {
    .footer__container {
      grid-template-columns: 1fr 2fr 1fr;
      gap: 0; } }

.footer-menu {
  display: flex;
  align-items: center;
  align-self: center;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 10px; }

.footer-menu__link {
  color: white; }
